import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  // Switch,
  // Route,
  // Link
} from "react-router-dom";
// import io from 'socket.io-client';
import './App.css';

import Header from './components/Header';
import SideNavi from './components/SideNavi';
import Content from './components/Content';
import SideInfo from './components/SideInfo';
import Footer from './components/Footer';
import socket from "./components/SocketIO";

import { Container, Row, Col } from "react-bootstrap";

function App() {
  let [width, setWidth] = useState(window.innerWidth);
  // let [height, setHeight] = useState(window.innerHeight);
  let [responseTime, setResponseTime] = useState(Date.now());

  // Footer
  let [visible, setVisibility] = useState(false);
  let isAtTheBottom = () => (window.innerHeight + window.scrollY) >= document.body.clientHeight - 20;

  window.addEventListener("load", () => { setResponseTime(Date.now() - responseTime); setVisibility(isAtTheBottom()); });
  window.addEventListener("resize", () => { setWidth(window.innerWidth); /*setHeight(window.innerHeight);*/ });
  window.addEventListener("scroll", () => { setVisibility(isAtTheBottom()); });

  socket.emit("connection");

  let minW = (a, b) => width < 970 ? a : b;

  return <Router>
    <Container fluid>
      <Row>
        <Col aria-colspan={3}>
          <Header responseTime={responseTime} />
        </Col>
      </Row>
      <Row className="middle-row">
        <Col className="navi-col" style={{ display: minW("none", "") }}><SideNavi /></Col>
        <Col className="middle-col" style={minW({}, { minWidth: 650 })}><Content /></Col>
        <Col className="side-col" style={{ display: minW("none", "") }}><SideInfo /></Col>
      </Row>
      <Row className="footer-row fixed-bottom" style={{ visibility: visible ? "visible" : "hidden" }}>
        <Col aria-colspan={3}>
          <Footer />
        </Col>
      </Row>
    </Container>
  </Router>
}

export default App;
